<template>
<!--  -->
  <div class="forgot">
      <div class="forgotbuy">
          <h2>{{ title }}</h2>
          <div class="forgotnew">
              <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" class="demo-ruleForm">
                    <el-form-item prop="passd">
                        <el-input :placeholder="yuan" type="password" v-model.number="ruleForm2.passd"></el-input>
                    </el-form-item>
                    <el-form-item prop="pass">
                        <el-input :placeholder="ned" type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item prop="checkPass">
                        <el-input :placeholder="conf" type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
                     
                     </el-form-item>
                    <el-form-item class="adada">
                        <el-button type="primary" @click="submitForm('ruleForm2')">{{ buttone }}</el-button>
                    </el-form-item>
                </el-form>
          </div>
      </div>
  </div>
</template>
<script>
// 
export default {
    data() {
    var checkAge = (rule, value, callback) => {
        var regExp = /^[A-Za-z]+[0-9]+[A-Za-z0-9]*|[0-9]+[A-Za-z]+[A-Za-z0-9]*$/g;
        if (value === '') {
          callback(new Error(''))
        } else if (regExp.test(value) === false) {
          callback(new Error('8-16'))
        } else {
          callback()
        }
      };
      var validatePass = (rule, value, callback) => {
      	var regExp = /^[A-Za-z]+[0-9]+[A-Za-z0-9]*|[0-9]+[A-Za-z]+[A-Za-z0-9]*$/g;
        if (value === '') {
          callback(new Error(''));
        } 
        if (regExp.test(value) === false) {
        	callback(new Error('8-16'))
        }
        else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePasss = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        title: 'RETURNS AND EXCHANGES',
        yuan: 'Old Password',
        ned: 'New Password',
        conf: 'Confim Password',
        buttone: 'SUBMlT',
        ruleForm2: {
          pass: '',
          checkPass: '',
          passd: '',
        },
        rules2: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePasss, trigger: 'blur' }
          ],
          passd: [
            { validator: checkAge, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
}
</script>

<style>
/* div */
    .forgot {
        width: 1080px;
        height: 100%;
        margin: 0 auto;
        margin-top: -15px;
    }
    /*  */
    .forgotbuy {
        float: right;
        width: 860px;
        height: 100%;
    }
    .forgotbuy h2 {
        width: 100%;
        height: 25px;
        font-size: 16px;
        color: #0F0F0F;
        line-height: 20px;
        border-bottom: 1px solid #b7b7b7;
    }
    /*  */
    .forgotnew {
        width: 370px;
        margin: 0 auto;
        margin-top: 50px;
    }
    .forgotnew .el-button {
        width: 100%;
        border-radius: 0;
        border-color: #e54c87;
        background: #e54c87;
        height: 36px;
    }
    .forgotnew .el-button:active{
      border-color: #ccc;
      background: #ccc;
    }
    .forgotnew .el-input__inner {
        border-radius: 0;
    }
.el-form-item.is-error .el-input__inner, .el-form-item.is-error .el-textarea__inner {
    border-color: #E54C87;
}
.el-form-item.is-error .el-input__inner, .el-form-item.is-error .el-textarea__inner:active {
  border-color: #E54C87;
}
.el-form-item.is-error .el-input__inner, .el-form-item.is-error .el-textarea__inner {
  border-color: #E54C87;
}
</style>


